import url from "../../../bin/url";
export default {
  props: ["listId"],
  data() {
    return {
      key: "value",
      goodsList: ""
    };
  },
  methods: {
    goDetail(item){
      this.$router.push('/goodsdetail?id='+item.id);
    },
    getGoodsList() {
      let _obj = {
        openId: localStorage.getItem("openId"),
        id: this.listId || "",
        size: "10",
        current: "1"
      };
      this.$fetch.post(url.getGoodsList, _obj).then(
        data => {
          console.log(data);
          if (data.code == 0) {
            this.goodsList = data.obj;
            this.goodsList.forEach(item => {
              item.picId = url.imgUrl + item.picId;
              console.log(item.picId);
            });
          }else{
             alert(data.msg)
          }
        },
        err => {
          alert("网络缓慢。。");
        }
      );
    }
  },
  mounted() {
       this.$refs.getwhole.$emit('goToList1',()=>{
           console.log('788900')
       })
    this.getGoodsList(); //获取全部列表
  }
};



